import getWin from '../helpers/get-window';
import { ecd as qecd } from './context-datas';
import { setCookie, readCookie } from '../helpers/cookie-functions';
import getQueryParam from '../helpers/get-query-param';

const win = getWin();

if (win) {
	win.ecdwnlC = '';
}

/*
 * Has logic to call a bunch of global variables that the rest of the beacon sets and creates
 * the 9 month matchback cookie. I left the beacon specific cookie code as we will launch this
 * on some properties that won't have the webmd shim along with our cookie functions
 */
export default () => {
	const dateExpire = new Date();
	// eslint-disable-next-line no-unused-vars
	const uPermC = String(readCookie('WBMD_PERM'));
	// eslint-disable-next-line no-unused-vars
	const uAuth = readCookie('WBMD_AUTH');
	const ecd = qecd();
	let regId = '';

	// Set Expire time to 9 months
	dateExpire.setMonth(dateExpire.getMonth() + 9);

	if (ecd !== '' && ecd.indexOf('wnl') >= 0) {
		setCookie('ecdwnl', 'wnl', dateExpire);

		win.ecdwnlC = 'wnl';

		if (uAuth === '') {
			const regIdQ = unescape(getQueryParam('mb'));
			const regIdQNoHash = regIdQ.split('#');

			if (regIdQ !== '') {
				setCookie('WBMD_MB9', regIdQNoHash[0], dateExpire);
				regId = regIdQ;
			}
		}
	}

	return regId;
};
