/* eslint-disable camelcase */
import getWin from './get-window';

const win = getWin();

export default () => {
	if (win) {
		win.aamPromise = new Promise((resolve) => {
			let aamCounter = 0;
			const checkAAM = setInterval(() => {
				aamCounter += 1;

				const startAnalytics = () => {
					resolve();
					clearInterval(checkAAM);
				};

				if (typeof AppMeasurement_Module_AudienceManagement === 'function') {
					startAnalytics();
				} else if (aamCounter > 50) {
					startAnalytics();
				}
			}, 100);
		});
	}
};
