/**
 * Metric Directive
 */
import Vue from 'vue';
import linktext from '../helpers/set-linktext';
import getWin from '../helpers/get-window';

const win = getWin();

const setNewDataAttr = (key, value, el) => {
	if (key && !el.attributes[`data-metrics-${key}`]) {
		el.setAttribute(`data-metrics-${key}`, value);
	}
};

// extracts data-metrics atrributes in key-value pairs
const getDataMetricsAttributes = (node) => {
	const keyvalues = {};
	Object.entries(node.dataset).map((items) => {
		if (items[0].indexOf('metrics') > -1) {
			const key = items[0].replace('metrics', '').toLowerCase();
			// eslint-disable-next-line prefer-destructuring
			keyvalues[key] = items[1];
		}
		return true;
	});
	return Object.entries(keyvalues);
};

/*
 * Creating this function for two reasons:
 * - element.closest function is not supported in IE
 * - element.closest does not include current element, which is supported in the old jQuery usage
 */
const findClosest = (el, selector) => {
	let matchesFn;
	let parent;
	let ele = el;

	// find vendor prefix
	['matches', 'webkitMatchesSelector', 'mozMatchesSelector', 'msMatchesSelector', 'oMatchesSelector'].some((fn) => {
		if (typeof document.body[fn] === 'function') {
			matchesFn = fn;
			return true;
		}
		return false;
	});

	// Check the current element first
	if (ele && ele[matchesFn](selector)) {
		return ele;
	}

	// then traverse parents
	while (ele) {
		parent = ele.parentElement;
		if (parent && parent[matchesFn](selector)) {
			return parent;
		}
		ele = parent;
	}

	return null;
};

/**
 * crawls the dom and reads data values to return the metrics value of an element
 * @see bindDataMetricsModule
 * @param  {Object} el "a" or "button" element
 * @return {String} the metrics value of element
 */
const getDataMetricsValue = (el) => {
	let metricsValue;
	let linkMetricsValue = '';
	let elementSelector;
	let moduleToLinkSeperator = '_';
	let sectionToLinkSeperator = '-';
	let allElementsFoundBySelector;
	let sectionValue = '';
	let contextElement;
	let hasMetricsSection = false;

	// we're going to do stuff in order to make this code as efficient as possible
	const moduleElement = findClosest(el, '*[data-metrics-module]');

	// if we don't find a module metrics call, go ahead and get out, we don't need to do anything
	if (!moduleElement) {
		return false;
	}

	// we are to get the metrics-section value
	const metricsSection = findClosest(el, '*[data-metrics-section]');

	if (metricsSection) {
		sectionValue = metricsSection.getAttribute('data-metrics-section');

		if (sectionValue !== '') {
			hasMetricsSection = true;
		}
	}

	// we can defintely grab this as we've verified that by now, there is this data attribute
	const moduleMetricsValue = moduleElement.getAttribute('data-metrics-module');

	/*
	 * now we try to grab the link value override. If this exists,
	 * we'll use it, no need to crawl looking for dynamic naming
	 */
	const link = findClosest(el, '*[data-metrics-link]');

	// if we have one of these, use it for the link metrics value, otherwise, do it dynamically
	if (link) {
		linkMetricsValue = link.getAttribute('data-metrics-link');
	} else {
		// grab the element selector if it exists

		// we need to determine the context element first: which can either be a module or section
		contextElement = hasMetricsSection ? metricsSection : moduleElement;

		elementSelector = contextElement.getAttribute('data-metrics-sel');

		// if so, let's use it
		if (elementSelector) {
			// grab all the elements found by selector
			allElementsFoundBySelector = contextElement.querySelectorAll(elementSelector);
			let index = 1;

			/*
			 * in this set of elements, find the one we clicked on,
			 * this is our metrics value. Add 1, since that's how .index works
			 */
			// eslint-disable-next-line no-restricted-syntax
			for (const element of allElementsFoundBySelector) {
				if (element === el.parentNode) {
					linkMetricsValue = index;
				} else {
					// eslint-disable-next-line no-plusplus
					index++;
				}
			}
		}
	}

	if (hasMetricsSection) {
		// this 'if' will prevent orphaned '-' at the end...
		if (linkMetricsValue === '') {
			sectionToLinkSeperator = '';
		}
		metricsValue = moduleMetricsValue + moduleToLinkSeperator + sectionValue
            + sectionToLinkSeperator + linkMetricsValue;
	} else {
		/*
		 * remove separator '_' when either metrics-module
		 * or metrics-link is an empty string.
		 * this provides flexibility of passing metrics values
		 * that do not require a link ID or separator
		 */
		if (moduleMetricsValue === '' || linkMetricsValue === '') {
			moduleToLinkSeperator = '';
		}
		metricsValue = moduleMetricsValue + moduleToLinkSeperator + linkMetricsValue;
	}

	return metricsValue;
};

/**
 * Global click handler for metrics (moduleID_linkID). No more onclicks.
 * We are catching any clicks on a elements and firing off some metrics
 *
 * This uses HTML5 data attributes to select HTML elements to create metrics
 *
 * - data-metrics-module: <required> This will be the module value.
 * It should be on the module containing div, as clicks on a elements will look
 * up in the DOM tree until they find a metrics-module value and use that.
 * There can be multiples of this on a module, and any link will
 * use the closest one to it as the module value. If this is left off the
 * parent module, there will be no metrics calls.
 *
 * - data-metrics-sel: <optional> This is the selector to get link ID's
 * and should be placed on the same node as metrics-module
 * (unless metrics-section is being used). Since most modules have simple
 * 1-X link id naming, this allows for us to not have to specify
 * this for every link. The code will use the metrics-module node and
 * .find this selector and grab the clicked on index to pass into the link value.
 * If this, as well as the metrics-link on a particular link is missing,
 * the code will pass a link id of 0
 *
 * - data-metrics-link: <optional> This is the override to metrics-sel.
 * If this data attribute exists on a link or one of it's parents,
 * it will be used for the link id. If this is missing, the code will
 * look for it on the closest parent. If it's not found,
 * metrics-sel to pick link id. If both are missing, 0 will be passed
 *
 * - data-metrics-section: <optional>.  If present, its value will be
 * placed in front of the metrics-link value with a dash as the separator between the 2.
 * This serves as an convenience attribute for the requirement that
 * divides multiple sections within a given metric-module.
 * If data-metrics-sel is used, it should be placed on the same element
 * as this data-metrics-section.
 * Sample usage can be found in the footer section of any harmony pages.
 *
 * - data-metrics-type: <optional> "track" or "ad-hoc". The default is "track",
 * which is a standard wmdTrack call when going to a new page or calling a
 * dynamic page view right after. If your a element is instead triggering some
 * sort of page action that doesn't result in a new
 * page view, you can specify "ad-hoc" and the code will make a wmdPageLink call instead.
 *
 */
const bindDataMetricsModule = (el, type) => {
	linktext(el);
	const metricsValue = getDataMetricsValue(el);

	// if we don't find a module metrics value, go ahead and get out, we don't need to do anything
	if (!metricsValue) {
		return;
	}

	// let's see if we have a type override
	let metricType = findClosest(el, '*[data-metrics-type]');

	if (metricType) {
		metricType = metricType.getAttribute('data-metrics-type');
	} else {
		metricType = 'track';
	}

	// and we'll call track or link based on that possible override
	if (type === 'dpv') {
		win.wmdDpv({
			pageName: el.getAttribute('data-metrics-pagename') || '',
			moduleName: metricsValue,
			iCount: el.getAttribute('data-metrics-icount') || '',
			s_sponUri: el.getAttribute('data-metrics-sponuri') || '',
			refresh: true,
		});
	} else if (metricType === 'track') {
		win.wmdTrack({
			moduleName: metricsValue,
		});
	} else {
		win.wmdPageLink({
			moduleName: metricsValue,
		});
	}
};

// use a event handler to avoid multiple calls;
const bindDataMetricsModuleHandler = (event, type) => {
	bindDataMetricsModule(event.currentTarget, type);
};

const bindClickEvents = (el, binding) => {
	// Converting Object into Array so we can iterate over easily
	// eslint-disable-next-line max-len
	const keyValues = binding.value ? Object.entries(binding.value) : getDataMetricsAttributes(el);
	// eslint-disable-next-line no-empty

	// Check if we have values
	if (keyValues) {
		keyValues.map((item) => {
			// Set the new data attributes on the current element
			setNewDataAttr(item[0], item[1], el);

			/*
			 * Check if this item is a module, "children nodes" here includes the original element,
			 * since we support module + link on the same element
			 */
			if (item[0] === 'module' || el.attributes['data-metrics-module']) {
				const childrenNodes = el.getElementsByTagName('*');
				const childrenArr = [el, ...childrenNodes];

				childrenArr.map((child) => {
					// Check if the child node is a button or anchor tag
					if (child.nodeName === 'BUTTON' || child.nodeName === 'A') {
						if (binding.arg !== 'dpv') {
							// Set a click event on tags
							child.addEventListener('click', bindDataMetricsModuleHandler);
						}
					}

					return true;
				});
			}

			return true;
		});
	}

	// Adding Click Event to Tags that are not Buttons or A tags
	if (binding.arg === 'track') {
		el.addEventListener('click', (e) => bindDataMetricsModuleHandler(e, 'custom'));
	}

	// Adding Click Event to Tags for Dynamic Pageviews
	if (binding.arg === 'dpv') {
		el.addEventListener('click', (e) => bindDataMetricsModuleHandler(e, 'dpv'));
	}
};

const directive = {
	bind(el, binding) {
		// Converting Object into Array so we can iterate over easily
		const keyValues = binding.value ? Object.entries(binding.value) : undefined;

		// if the developer want to fire the omniture manually set is disabled
		if (binding.arg === 'disable') {
			if (keyValues) {
				keyValues.map((item) => {
					// Set the new data attributes on the current element
					setNewDataAttr(item[0], item[1], el);
					return true;
				});
			}
		} else {
			bindClickEvents(el, binding);
		}
	},
	componentUpdated(el, binding) {
		bindClickEvents(el, binding);
	},
};

Vue.prototype.$bindMetrics = bindClickEvents;

export default directive;
