import log from './logger';

export default (type, options) => {
	let trackingServer = '';

	if (type === 'nonSecure') {
		if (typeof options.trackingServers !== 'undefined') {
			if (typeof options.trackingServers.nonSecure !== 'undefined') {
				trackingServer = options.trackingServers.nonSecure;
			}
		} else {
			trackingServer = 'std.o.webmd.com';
		}
		log('info', `s_md.trackingServer: ${trackingServer}`);
	} else if (type === 'secure') {
		if (typeof options.trackingServers !== 'undefined') {
			if (typeof options.trackingServers.secure !== 'undefined') {
				trackingServer = options.trackingServers.secure;
			}
		} else {
			trackingServer = 'ssl.o.webmd.com';
		}
		log('info', `s_md.trackingServer: ${trackingServer}`);
	}

	return trackingServer;
};
