import getWin from '../helpers/get-window';
import checkForExistence from '../helpers/check-for-existence';
import setEvent from '../helpers/set-events';

const win = getWin();

export default () => {
	if (checkForExistence(win.s_light_reg) !== '') {
		setEvent(win.s_light_reg.toLowerCase());
	} else if (checkForExistence(win.s_new_reg) !== '') {
		setEvent(win.s_new_reg.toLowerCase());
		win.s_new_reg = '';
	}
};
