import getWin from './helpers/get-window';
import log from './helpers/logger';
import coreSetup from './core-setup';

const win = getWin();

export default (defaultOptions, doPV) => {
	log('info', 'CLEAR METRICS');

	win.s_md.clearVars();
	win.s_md.contextData = {};

	const resetEvent = new CustomEvent('webmd.pagedata.reset');
	document.dispatchEvent(resetEvent);

	coreSetup(defaultOptions, true, doPV);

	// Custom Code Here
};
