import log from './logger';
import checkForExistence from './check-for-existence';
import getWin from './get-window';

const win = getWin();

export default (omniture, options) => {
	let account = checkForExistence(omniture.s_account);

	if (win.location.search.indexOf('runtime') > -1) {
		return 'webmddev';
	}

	if (account !== 'webmddev') {
		account = 'webmdp1global';
	} else if (checkForExistence(options.reportSuite) !== '') {
		account = options.reportSuite;
	}

	log('info', `Report Suite: ${account}`);

	return account;
};
