export default (url) => {
	let urlClean = url;

	urlClean = urlClean.toLowerCase();
	urlClean = urlClean.split('https://www.').join('');
	urlClean = urlClean.split('http://www.').join('');
	urlClean = urlClean.split('https://').join('');
	urlClean = urlClean.split('http://').join('');
	urlClean = urlClean.split('www.').join('');
	urlClean = urlClean.split('.html').join('');
	urlClean = urlClean.split('.htm').join('');
	urlClean = urlClean.split('.aspx').join('');
	urlClean = urlClean.split('.asp').join('');
	urlClean = urlClean.split(' ').join('-');
	urlClean = urlClean.split('default').join('');
	urlClean = urlClean.split(' ').join('-');
	urlClean = urlClean.split('%20').join('-');
	urlClean = urlClean.split('_').join('-');
	urlClean = urlClean.split('\\').join('');
	urlClean = urlClean.split('\'').join('');

	return urlClean;
};
