import { setContextData } from '../helpers/set-omniture';

export const truncate = (link) => {
	if (link === 'undefinded' || link === '') {
		return null;
	}

	const segment = link.split('_');

	// When link is greater than 100, truncate and map to objects
	if (link.length > 100) {
		if (segment.length < 2) {
			return segment;
		}

		// Truncate or parse the first and second positioned elements in seg Array
		segment[0] = (segment[0].length > 73) ? segment[0].substring(0, 72) : segment[0];
		segment[1] = (segment[1].length > 20) ? segment[1].substring(0, 19) : segment[1];
	}

	return segment;
};

export default (link) => {
	const segment = truncate(link);
	let mpage = '';
	let mmodule = '';
	let mlink = '';

	if (segment !== null && segment.length === 2) {
		mpage = segment[0].toLowerCase();
		mmodule = segment[1].toLowerCase();
	} else if (segment !== null && segment.length === 3) {
		mpage = segment[0].toLowerCase();
		mmodule = segment[1].toLowerCase();
		mlink = segment[2].toLowerCase();
	} else if (segment !== null && segment.length === 4) {
		mpage = segment[0].toLowerCase();
		mmodule = segment[1].toLowerCase();
		mlink = `${segment[2].toLowerCase()}_${segment[3].toLowerCase()}`;
	}

	setContextData('wb.mpage', mpage);
	setContextData('wb.mmodule', mmodule);
	setContextData('wb.mlink', mlink);
};
