/* eslint-disable no-param-reassign,max-len,consistent-return,no-underscore-dangle,camelcase */
// this is the main entry of omniture plugin
import directive from './directives/directive';
import OmnitureComponent from '../Omniture.vue';
import getWin from './helpers/get-window';
import safetyCheck from './helpers/safety-check';
import checkForAAM from './helpers/check-for-aam';
import log from './helpers/logger';
import coreSetup from './core-setup';
import clearMetrics from './clear-metrics';
import {
	wmdPageview, wmdPageLink, wmdTrack, wmdDpv, wmdSearchTrack,
} from './trackers/omniture-functions';
import { setCookie, readCookie, deleteCookie } from './helpers/cookie-functions';
import getClosestMetric from './helpers/data-metric-closest';

const win = getWin();

if (win) {
	win.vueMetricsVersion = '2.5.2';
	win.biConsumerIsReady = false;

	// Set easier refrence to Intial State
	win.initialState = win.__INITIAL_STATE__ || {};
}

// export default Metric plugin;
const metricplugin = {
	install(Vue, opts) {
		const isEmpty = (obj) => [Object, Array].includes((obj || {}).constructor) && !Object.entries((obj || {})).length;
		let defaultOptions = {
			beaconType: 'core',
			trackingServers: {
				nonSecure: 'std.o.webmd.com',
				secure: 'ssl.o.webmd.com',
			},
		};

		// Overriding defaults if we have any new options
		defaultOptions = Object.assign(defaultOptions, opts);

		/* Start Core Setup */
		if (win) {
			const errorMessage = 'NO INITIAL STATE SET ON THE PAGE!!!';
			const hasInitialState = !isEmpty(win.initialState);

			// win.document.addEventListener('onAnalyticsReady', () => (hasInitialState ? coreSetup(opts) : log('error', errorMessage)));

			// Run Promise which checks for AAM
			checkForAAM();

			// If AppMeasurment is loaded we can load AAM Manually then kick off coreSetup as long as we have InitialState
			win.aamPromise.then(() => (hasInitialState ? coreSetup(defaultOptions) : log('error', errorMessage)));
		}

		/* Omniture Component */
		Vue.component('Omniture', OmnitureComponent);

		/* Metric Directive */
		Vue.directive('metric', directive);

		/* WebMD Core Functions */
		Vue.prototype.$vwmdPageview = (settings, options) => safetyCheck(wmdPageview, settings, options);
		Vue.prototype.$vwmdPageLink = (settings, options) => safetyCheck(wmdPageLink, settings, options);
		Vue.prototype.$vwmdTrack = (settings, options) => safetyCheck(wmdTrack, settings, options);
		Vue.prototype.$vwmdDpv = (settings, options) => safetyCheck(wmdDpv, settings, options);
		Vue.prototype.$vwmdSearchTrack = (settings, options) => safetyCheck(wmdSearchTrack, settings, options);
		Vue.prototype.$vwmdReset = (doPV) => clearMetrics(defaultOptions, doPV);

		/* Cookie Functions */
		Vue.prototype.$vsetCookie = (key, value) => safetyCheck(setCookie, key, value);
		Vue.prototype.$vreadCookie = (key) => safetyCheck(readCookie, key, 'readNow');
		Vue.prototype.$vdeleteCookie = (key) => safetyCheck(deleteCookie, key, null);

		/* Data Metric Closest Funtions */
		Vue.prototype.$vgetClosestModule = (selector, options) => getClosestMetric(selector, options, 'module');
		Vue.prototype.$vgetClosestMlink = (selector, options) => getClosestMetric(selector, options, 'link');

		/* Legacy Funtions - Limited or No Support */
		Vue.prototype.$vwmddpv = (settings, options) => Vue.prototype.$vwmdDpv(settings, options);
		Vue.prototype.$vwmdSetContext = () => log('error', 'NO MORE SUPPORT FOR vwmdSetContext method');
		Vue.vsetSinglePropValue = () => log('error', 'NO MORE SUPPORT FOR vsetSinglePropValue method');

		if (win) {
			win.wmdReset = Vue.prototype.$vwmdReset;
		}
	},
};

export default metricplugin;
