import getWin from './get-window';

const win = getWin();

export default (key, value, type, callback) => {
	let originalValue = '';
	let metricType = '';

	/**
	 * If value is not a string we will return out and not continue beacue
	 * props and contextData can only set string values
	 */
	if (typeof value !== 'string') {
		return;
	}

	if (key.indexOf('prop') > -1) {
		originalValue = win.s_md[key];
		metricType = 'prop';

		win.s_md[key] = value.toLowerCase();

		if (type === 'adhoc') {
			win.s_md.linkTrackVars += `,${key}`;
		}

		callback(originalValue, metricType);
	} else if (key.indexOf('wb.') > -1) {
		originalValue = win.s_md.contextData[key];
		metricType = 'contextData';

		win.s_md.contextData[key] = value.toLowerCase();

		if (type === 'adhoc') {
			win.s_md.linkTrackVars += `,contextData.${key}`;
		}

		callback(originalValue, metricType);
	}
};
