import getWin from './get-window';

const win = getWin();

export default (beaconType) => {
	let metricVersion = '';
	let sVer = '';

	if (win && win.vueMetricsVersion) {
		metricVersion = win.vueMetricsVersion;
	}

	sVer = `|consumer-core|${metricVersion}`;

	if (typeof beaconType !== 'undefined' && beaconType !== '') {
		switch (beaconType) {
			case 'core':
				sVer = `|consumer-core|${metricVersion}`;
				break;
			case 'vitals':
				sVer = `|vitals|${metricVersion}`;
				break;
			default:
				sVer = `|consumer-core|${metricVersion}`;
		}
	}

	return sVer;
};
