import getWin from '../helpers/get-window';
import log from '../helpers/logger';
import sGetLoadTime from '../helpers/get-load-time';
import sGetResponsiveWidth from '../helpers/get-responsive-width';
import getQueryParam from '../helpers/get-query-param';
import checkForExistence from '../helpers/check-for-existence';
import getTrackingCookie from '../helpers/get-tracking-cookie';
import { readCookie } from '../helpers/cookie-functions';
import getMeta from '../helpers/get-meta';
import { setContextData } from '../helpers/set-omniture';

const win = getWin();

/**
 * Store
 * This sets any custom Context Datas that get set in the store
 */
export const store = (omniture) => {
	const contextDataArray = typeof (omniture.context) !== 'undefined' ? Object.entries(omniture.context) : [];

	if (contextDataArray.length > 0) {
		contextDataArray.forEach((metric) => {
			setContextData(metric[0], metric[1]);
			log('info', `s_md.contextData.wb.${metric[0]}: ${metric[1]}`);
		});
	}

	return '';
};

/**
 * Page Load Time
 */
export const plt = () => sGetLoadTime();

/**
 * Responsive Breakpoint Width
 */
export const brkpt = () => sGetResponsiveWidth();

/**
 * ICD Query Param
 */
export const icd = () => getQueryParam('icd');

/**
 * ECD Query Param
 */
export const ecd = () => {
	// eslint-disable-next-line no-shadow
	let ecd = getQueryParam('ecd');

	if (ecd !== '') {
		let vendor = getQueryParam('v');
		let publisher = getQueryParam('p');

		if (vendor !== '') {
			vendor = vendor.split('_').join('-');
			ecd = (ecd.lastIndexOf('_') === 0) ? ecd + vendor : `${ecd}_${vendor}`;
		}

		if (publisher !== '') {
			publisher = publisher.split('_').join('-');
			ecd += `_${publisher}`;
		}

		ecd = ecd.toLowerCase();
		ecd = ecd.split('__').join('_').split('%20').join('-');
	}

	return ecd;
};

/**
 * Visitor API Presence
 */
export const vapi = () => {
	const visitorAPI = typeof (win.Visitor) !== 'undefined' ? 'visitorapi present' : 'visitorapi missing';

	return visitorAPI;
};

/**
 * Embedded Asset
 */
export const embast = () => {
	let embAsset = checkForExistence(win.s_embedded_asset);

	if (embAsset && win.s_embedded_asset === true) {
		embAsset = 'embedded-asset';
	}

	return embAsset;
};

/**
 * Previous WebMD Page
 */
export const cstm = () => {
	let url = '';

	if (typeof win.document.referrer !== 'undefined' && win.document.referrer.match(/\.webmd(\.boots)?\.com/gi)) {
		if (checkForExistence(win.s_md.pageName) !== '') {
			url = `landpg::${win.s_md.pageName}`;
		}
	}

	return url;
};

/**
 * Liveramp
 */
export const liveramp = () => getTrackingCookie('lrid');

/**
 * Publication Type
 */
export const pbtp = (omniture) => checkForExistence(omniture.s_pbtp);

/**
 * Publication Name
 */
export const pbnm = (omniture) => checkForExistence(omniture.s_pbnm);

/**
 * Publication Topic Outline
 */
export const tpcot = (omniture) => checkForExistence(omniture.s_tpcot);

/**
 * Lotame ID
 */
export const lotprofid = () => readCookie('lpid');

/**
 * Publication Source
 */
export const pubsource = (omniture) => checkForExistence(omniture.s_publication_source);

/**
 * Publication Source Company
 */
export const pubsourceco = (omniture) => checkForExistence(omniture.s_company);

/**
 * Meta Keywords
 */
export const metakywrd = () => {
	const { meta } = win.initialState.pagedata || {};

	return checkForExistence(meta.meta_keywords);
};

/**
 * Intended Demographics
 */
export const intdemo = (omniture) => {
	const intdDemo = checkForExistence(omniture.s_company) && checkForExistence(omniture.s_intended_demographics) !== '__' ? checkForExistence(omniture.s_intended_demographics) : '';

	return intdDemo;
};

/**
 * Stage Condition
 */
export const stgcond = (omniture) => checkForExistence(omniture.s_stage_condition);

/**
 * sMRT
 */
export const mrt = (omniture) => checkForExistence(omniture.s_mrt);

/**
 * Page Title with OG Title Fallback
 */
export const titletag = () => {
	const metaTitle = getMeta('og:title') || '';
	const titleEle = win.document.querySelector('article.current header h1') || win.document.querySelector('article header h1') || false;
	let title = '';

	if (titleEle) {
		title = titleEle.innerText.toLowerCase();
	} else if (metaTitle && metaTitle !== '') {
		title = metaTitle.toLowerCase();
	}

	return title;
};

/**
 * TapID
 */
export const tapid = () => readCookie('TapAd_DID');

/**
 * gdprauth
 */
 export const gdprauth = () => {
	if (window.countryBlocked && (webmdCcm.optanonCookie.goBlock || webmdCcm.optanonCookie.noCookie)){
		return "no";
	}
	
	return "yes";
};
