import getWin from './get-window';

const win = getWin();

export const setCookie = (key, value, expire) => win.s_md.Util.cookieWrite(key, value, expire);

export const readCookie = (key, option) => {
	let cookieValue;

	if (option === 'readNow') {
		/**
		 * Custom Read Cookie function so we can read cookies before
		 * the beacon code has been fully initialized
		 */
		const getCookieValue = (a) => { const b = document.cookie.match(`(^|[^;]+)\\s*${a}\\s*=\\s*([^;]+)`); return b ? b.pop() : ''; };

		cookieValue = decodeURI(getCookieValue(key));
	} else {
		/**
		 * If beacon code has been initialized we will use the built in utility
		 */
		cookieValue = win.s_md.Util.cookieRead(key);
	}

	return cookieValue;
};

export const deleteCookie = (key) => win.s_md.Util.cookieWrite(key, '', new Date());
