import getWin from './get-window';

const win = getWin();

export default (callBack, settings, options) => {
	let callBackValue;

	if (win) {
		if (win.biConsumerIsReady) {
			callBackValue = callBack(settings, options);
		} else if (options && options === 'readNow') {
			callBackValue = callBack(settings, options);
		} else {
			/*
			 * during the loading biconsumerjs time, if there are omniture functions is fired,
			 * put it in the FIFO array. after loading, solve them.
			 */
			win.omnitureWaitingList = win.omnitureWaitingList || [];
			win.omnitureWaitingList.push({
				callBack,
				settings,
				options,
			});

			win.document.addEventListener('onAdobeFireSaftyCheck', () => {
				if (win.omnitureWaitingList) {
					while (win.omnitureWaitingList.length > 0) {
						const func = win.omnitureWaitingList.shift();
						callBackValue = func.callBack(func.settings, func.options);
					}
				}
			}, {
				once: true,
			});
		}
	}

	return callBackValue;
};
