import getWin from './get-window';

const win = getWin();

export default (url) => {
	if (!win) {
		return;
	}

	// Create a variable to track link text
	win.s_linktext = '';

	if (typeof url !== 'string') {
		win.s_linktext = url.innerText.replace(/[^\w\s]/g, '').replace(/\s+/g, ' ') || url.alt;
	}
};
