/* eslint-disable max-len,no-underscore-dangle */
import getWin from './get-window';
import { setContextData } from './set-omniture';

const win = getWin();

/**
 * This creates a list in a global variable. The list will be used in order to store
 *     elements using their metrics call as the key. These elements can then be accessed inside
 * the wmdPageLink function in order to retrieve it's attributes (like href, class, id) in order to
 * properly set the props for certain metrics calls.
 *
 * This function will just keep the name of the global variable in one location in case we need to change it.
 *
 * @return    Object     The list of elements that will be used inside wmdPageLink for metrics.
 */
const getWmdPageLinkElementList = () => {
	if (typeof win._wmdPageLinkList === 'undefined') {
		win._wmdPageLinkList = {};
	}
	return win._wmdPageLinkList;
};

/**
 * getGlobalWmdPageLinkVar() is called by wmdPageLink. this method checks if there's already
 * an element defined for the passed link and use that to pass it in c20 value.
 *  @param               string     link         the key to the item that will be returned
 *
 *  @return               Object returns the object from the private list
 */
const getGlobalWmdPageLinkVar = (link) => {
	const list = getWmdPageLinkElementList();
	let tmp;

	if (typeof list[link] !== 'undefined') {
		tmp = list[link];
		delete list[link];
	}

	return tmp;
};

export default (link) => {
	let element = getGlobalWmdPageLinkVar(link);

	if (typeof element !== 'undefined') {
		if (typeof element !== 'string' && typeof element.href !== 'undefined') {
			element = element.href.replace(/^https?:\/\/(www\.)?/gi, '');
		}

		if (element.indexOf('webmd.com') < 1) {
			setContextData('wb.exiturl', `exit::${element}`);
		}

		if (/(exe$)|(zip$)|(wav$)|(mp3$)|(mov$)|(mpg$)|(avi$)|(wmv$)|(doc$)|(pdf$)|(xls$)/i.test(element)) {
			setContextData('wb.dwnld', `dwnld::${element}`);
		}
	}
};
