/* eslint-disable no-underscore-dangle */
import getWin from './get-window';

const win = getWin();

/**
 * Types are error, warn, info and log
 * Message can be a String, Array or Object
 */
export default (type, message, code) => {
	if (win._satellite && win._satellite.logger) {
		win.document.dispatchEvent(new CustomEvent('onAdobeOmniLoggerReady'));
		win._satellite.logger[type](message, (typeof code === 'undefined' ? '' : code));
	} else {
		const callBack = () => win._satellite.logger[type](message, (typeof code === 'undefined' ? '' : code));

		if (win.loggerWaitingList) {
			win.loggerWaitingList.push({
				callBack,
			});
		} else {
			win.loggerWaitingList = [];
			win.loggerWaitingList.push({
				callBack,
			});
		}

		win.document.addEventListener('onAdobeOmniLoggerReady', () => {
			if (win.loggerWaitingList) {
				while (win.loggerWaitingList.length > 0) {
					const func = win.loggerWaitingList.shift();
					func.callBack(func.settings, func.options);
				}
			}
		});
	}
};
