import getWin from './get-window';

const win = getWin();

export const setContextData = (key, value) => {
	if (key) {
		// eslint-disable-next-line no-useless-escape
		const contextData = key.replace(/[^A-Za-z0-9\.]/g, '').toLowerCase();

		switch (typeof (value)) {
			case 'string':
				win.s_md.contextData[contextData] = value.toLowerCase();
				break;
			case 'number':
				win.s_md.contextData[contextData] = value;
				break;
			default:
		}
	}
};

export const setProp = (key, value) => {
	if (key) {
		win.s_md[key] = value;
	}
};

export const setEvar = (key, value) => {
	if (key) {
		win.s_md[key] = value;
	}
};
