import getWin from './get-window';

const win = getWin();

export default () => {
	let sLoadTime;

	const b = new Date().getTime();
	const o = win.performance ? performance.timing : 0;
	const a = o ? o.requestStart : win.inHeadTS || 0;

	sLoadTime = a ? Math.round((b - a) / 1000) : '';

	if (sLoadTime < 0 || sLoadTime > 300) {
		sLoadTime = '';
	}

	return sLoadTime;
};
