import getWin from './get-window';

const win = getWin();

// for the IE browser not going to support ie8 :D
export const addClosestFunction = () => {
	if (win) {
		if (!Element.prototype.matches) {
			Element.prototype.matches = Element.prototype.msMatchesSelector
				|| Element.prototype.webkitMatchesSelector;
		}

		if (!Element.prototype.closest) {
			// eslint-disable-next-line func-names
			Element.prototype.closest = function (s) {
				let el = this;
				if (!document.documentElement.contains(el)) return null;
				do {
					if (el.matches(s)) return el;
					el = el.parentElement || el.parentNode;
				} while (el !== null && el.nodeType === 1);
				return null;
			};
		}
	}
};

// eslint-disable-next-line no-unused-vars
export default (selector, options, type) => {
	const moduleType = type === 'module' ? 'data-metrics-module' : 'data-metrics-link';

	addClosestFunction();
	const linkNode = selector.closest(`*[${moduleType}]`);

	return linkNode ? linkNode.getAttribute(moduleType) : 'unableToFindModuleValue';
};
