export default (key, val) => {
	let value = '';
	let keyValue = key;

	if (val) {
		value = val;
	}

	/**
	 * If key is not a string we will set it to empty string so
	 * it wont error out and break the page.
	 */
	if (typeof keyValue !== 'string') {
		keyValue = '';
	}

	return typeof key !== 'undefined' ? keyValue : value.toLowerCase();
};
