import log from './logger';
import getWin from './get-window';
import { setCookie, readCookie, deleteCookie } from './cookie-functions';

const win = getWin();

export default (sSource, type, callback) => {
	const { omniture } = win.initialState.pagedata;
	const dateExpire = new Date();
	const sTopicId = typeof omniture.s_topic !== 'undefined' ? omniture.s_topic : '';
	const sContentClassification = typeof omniture.s_business_reference !== 'undefined' ? omniture.s_business_reference : '';
	const sHealthCenter = typeof omniture.s_channel_health !== 'undefined' ? omniture.s_channel_health : '';
	const sPageviewId = typeof win.s_pageview_id !== 'undefined' ? win.s_pageview_id : '';
	const sSponsorProgram = typeof omniture.s_sponsor_program !== 'undefined' ? omniture.s_sponsor_program : '';
	let sText = '';
	let sCtr = sSource;
	let cookiesToSet = [
		['ctr', ''],
		['ctrTopicId', ''],
		['ctrContentClassification', ''],
		['ctrHealthCenter', ''],
		['ctrPageviewId', ''],
		['prop29', ''],
	];
	const cookieToRead = {};

	if (type === 'write') {
		// Set Expire time to 60sec
		dateExpire.setSeconds(dateExpire.getSeconds() + 60);

		// Check for presence of linktext store in window
		if (typeof win.s_linktext !== 'undefined') {
			sText = win.s_linktext;

			// Reset win.s_linktext to empty
			win.s_linktext = '';
		}

		sCtr = `${win.s_md.pageName.split('_').join('-')}_${sSource}|${sText}`;
		sCtr = sCtr.toLowerCase();

		cookiesToSet = [
			['ctr', sCtr],
			['ctrTopicId', sTopicId],
			['ctrContentClassification', sContentClassification],
			['ctrHealthCenter', sHealthCenter],
			['ctrPageviewId', sPageviewId],
			['prop29', sSponsorProgram],
		];

		// Set each cookie
		cookiesToSet.forEach((cookieToSet) => {
			if (cookieToSet[1] !== '') {
				setCookie(cookieToSet[0], cookieToSet[1], dateExpire);
			}
		});

		log('info', `ctrCookie has wrote: expire - ${dateExpire}, ctr cookie - ${sCtr}, ctrTopicId - ${sTopicId}, ctrContentClassification - ${sContentClassification}, ctrHealthCenter - ${sHealthCenter}, ctrPageviewId - ${sPageviewId}, prop29 - ${sSponsorProgram}`);
	} else if (type === 'read') {
		// Read and Delete each cookie by setting to empty
		cookiesToSet.forEach((cookieToSet) => {
			cookieToRead[cookieToSet[0]] = readCookie(cookieToSet[0]);
			deleteCookie(cookieToSet[0]);
		});

		log('info', 'ctrCookie has read: ', cookieToRead);

		callback(cookieToRead);
	}
};
