import getWin from '../helpers/get-window';
import urlClean from '../helpers/url-clean';

const win = getWin();

export default (omiture) => {
	let url = '';

	if (win) {
		win.s_asset = '';
		win.s_sponUri = '';
	}

	url = omiture.s_pagename || '';

	if (url === '') {
		// eslint-disable-next-line prefer-destructuring
		url = String(document.location).split('?')[0].split('#')[0];
	}

	url = urlClean(url);

	if (omiture.s_movie) {
		url = `${url}?movie${omiture.s_movie}`;
	}

	if (omiture.s_asset_class === 'tools - sdc') {
		url = `${url}/formid=${win.s_md.Util.getQueryParam('formid')}`;
	}

	win.s_asset = url.substr(url.indexOf('/') + 1);
	win.s_asset = String(urlClean(win.s_asset)).toLowerCase();

	if (win.s_asset === '') {
		win.s_asset = 'default';
	}

	if (omiture.s_sponsor_brand || omiture.s_sponsor_program) {
		win.s_sponUri = win.s_asset;
	}

	if (omiture.s_page_suffix) {
		url = `${url}-${omiture.s_page_suffix}`;
	}

	return url;
};
