import getWin from '../helpers/get-window';
import { readCookie, deleteCookie } from '../helpers/cookie-functions';
import setNewMetric from '../helpers/set-new-metric';

const win = getWin();

export default () => {
	if (typeof win.s_md.prop40 === 'undefined' || win.s_md.prop40.length === 0) {
		const p40Cookie = readCookie('p40');

		if (p40Cookie !== 'undefined' && p40Cookie.length > 0) {
			const p40Split = p40Cookie.split('|');

			setNewMetric('prop40', p40Split[0]);

			if (p40Split.length === 2 && p40Split[1].length > 0) {
				const p40Prog = p40Split[1];

				if (p40Prog !== win.s_md.prop29 && p40Prog !== win.s_md.prop31) {
					setNewMetric('prop40', '');
					deleteCookie('p40');
				}
			}
		}
	}
};
