/* eslint-disable no-unused-vars */
import { createPvid } from 'webmd-core';
import getTrackingCookie from '../helpers/get-tracking-cookie';
import getWin from '../helpers/get-window';
import log from '../helpers/logger';
import readWriteCookies from '../helpers/read-write-cookies';
import urlClean from '../helpers/url-clean';
import { setContextData, setEvar, setProp } from '../helpers/set-omniture';
import { setCookie } from '../helpers/cookie-functions';
import setNewMetric from '../helpers/set-new-metric';
import getPvCandidate from '../helpers/get-pv-candidate';
import moduleTracking from './module-tracking';
import userSegment from './user-segment';
import checkForExistence from '../helpers/check-for-existence';
import setEvent from '../helpers/set-events';
import getExitDownloadLink from '../helpers/get-exit-download-link';
import { prop15, prop30 } from './props';

const win = getWin();

if (win) {
	win.s_persist40 = '';
}

/**
 * wmdPageview
 * @param {Object} settings key/values for sSource
 * @param {Object} options ...
 * This function takes specific settings that could
 * be passsed by other functions such as wmdDpv. We also allow
 * some options to be set if you wanted to modify the current
 * settings or global s_md object
 */
export const wmdPageview = (settings, options) => {
	const { omniture } = win.initialState.pagedata;
	const resetMetrics = [];
	let defaultSettings = {
		pageName: '',
		iCount: '',
		s_sponUri: '',
	};
	let defaultOptions = {
		metrics: {},
		resetMetrics: false,
	};

	defaultSettings = Object.assign(defaultSettings, settings);
	defaultOptions = Object.assign(defaultOptions, options);

	// Clear some settings
	setContextData('wb.lnktxt', '');

	if (win.s_pageview_sent) {
		setContextData('wb.ecd', '');
		setProp('prop15', '');
		moduleTracking('');
	}

	// Set Pageview Number
	if (defaultSettings.iCount !== '') {
		setProp('prop9', defaultSettings.iCount);
	}

	// Set New Pageview ID
	setProp('prop24', win.s_pageview_id);

	// Set prop 46 (TURN ID)
	setEvar('eVar46', getTrackingCookie('turn'));

	// Set New PageName & Prop45
	if (defaultSettings.pageName !== '') {
		const newPageName = urlClean(defaultSettings.pageName);

		win.s_md.pageName = newPageName;

		// Set Prop45
		if (omniture.s_sponsor_brand || omniture.s_sponsor_program) {
			defaultSettings.s_sponUri = newPageName.substr(newPageName.indexOf('/') + 1);
		}
		if (defaultSettings.s_sponUri !== '') {
			setProp('prop45', defaultSettings.s_sponUri);

			if (omniture.s_tug) {
				setProp('prop45', `${omniture.s_tug}_${defaultSettings.s_sponUri}`);
			}
		}
	}

	// Prop15
	setProp('prop15', prop15());

	// Read CTR Cookies set by the previous page
	readWriteCookies(null, 'read', (cookie) => {
		// Set CTR from previous page
		if (cookie.ctr !== '') {
			const ctrCookieRead = cookie.ctr.split('|');
			const ctrLocation = ctrCookieRead[0];

			// Set module tracking
			moduleTracking(ctrLocation);
		}

		// Set Topic ID from previous page
		if (cookie.ctrTopicId !== '') {
			setContextData('wb.mtopic', cookie.ctrTopicId);
		}

		// Set Content Classification from previous page
		if (cookie.ctrContentClassification !== '') {
			setContextData('wb.mcontentc', cookie.ctrContentClassification);
		}

		// Set Health Center from previous page
		if (cookie.ctrHealthCenter !== '') {
			setContextData('wb.mhcenter', cookie.ctrHealthCenter);
		}

		// Set Pageview ID from previous page
		if (cookie.ctrPageviewId !== '') {
			setContextData('wb.mpvid', cookie.ctrPageviewId);
		}
	});

	// User Segment
	userSegment();

	// Set New Metric
	const newMetricsArray = Object.entries(defaultOptions.metrics);
	newMetricsArray.forEach((metric) => {
		setNewMetric(metric[0], metric[1], null, (originalValue, metricType) => {
			if (defaultOptions.resetMetrics) {
				resetMetrics.push({
					key: metric[0],
					value: typeof originalValue === 'undefined' ? '' : originalValue,
					type: metricType,
				});
			}
		});
	});

	// Dispatch Custom Event - before_pv
	win.document.dispatchEvent(new CustomEvent('before_pv'));

	// Dispatch Custom Event - onAdobeOmniBeforePv
	win.document.dispatchEvent(new CustomEvent('onAdobeOmniBeforePv'));

	// Reset code post tracking call
	win.document.addEventListener('onAdobeOmniPostTrack', () => {
		// Reset Custom Props & Context Datas
		if (resetMetrics.length > 0) {
			resetMetrics.forEach((metric) => {
				if (metric.type === 'prop') {
					setProp(metric.key, metric.value);
				} else if (metric.type === 'contextData') {
					setContextData(metric.key, metric.value);
				}
			});
		}

		// Reset code based on if we have captured if once before
		setProp('prop15', '');
		moduleTracking('');

		win.s_pageview_sent = true;

		// Dispatch Custom Event - after_pv
		win.document.dispatchEvent(new CustomEvent('after_pv'));

		// Dispatch Custom Event - onAdobeOmniAfterPv
		win.document.dispatchEvent(new CustomEvent('onAdobeOmniAfterPv'));

		// Log
		log('info', 'FIRED WMDPAGEVIEW!!');
	}, {
		once: true,
	});

	// Fire Tracking Call
	win.s_md.t();

	// Get PV Candidate for Comscore - DO NOT DELETE!!
	getPvCandidate();
};

/**
 * wmdPageLink
 * @param {Object} settings key/values for sSource
 * @param {Object} options nothing used currently
 * This function takes the link ...
 */
export const wmdPageLink = (settings, options) => {
	const { omniture } = win.initialState.pagedata;
	const originalLinkTrackVars = win.s_md.linkTrackVars;
	const originalContextData = win.s_md.contextData;
	const originalEvents = win.s_md.events;
	const resetMetrics = [];
	let defaultSettings = {
		link: '',
		moduleName: '',
	};
	let defaultOptions = {
		metrics: {},
		resetMetrics: false,
	};

	/**
	 * Check if legacy coding pattern is passing the module
	 * tracking as a string instead of an object
	 */
	if (typeof settings === 'string') {
		// Set old string coding pattern to the new settings object
		defaultSettings.moduleName = settings;
	} else {
		defaultSettings = Object.assign(defaultSettings, settings);
	}

	defaultOptions = Object.assign(defaultOptions, options);

	if (defaultSettings.link !== '') {
		defaultSettings.moduleName = defaultSettings.link;
	}

	if (defaultSettings.moduleName !== '') {
		defaultSettings.moduleName = defaultSettings.moduleName.toLowerCase();
	}

	// Clear some settings
	setContextData('wb.lnktxt', '');
	setProp('prop15', '');

	// Setting Topic ID
	if (checkForExistence(omniture.s_topic)) {
		setContextData('wb.mtopic', omniture.s_topic);
	}

	// Setting Content Classification
	if (checkForExistence(omniture.s_business_reference)) {
		setContextData('wb.mcontentc', omniture.s_business_reference);
	}

	// Setting Channel Health
	if (checkForExistence(omniture.s_channel_health)) {
		setContextData('wb.mhcenter', omniture.s_channel_health);
	}

	// Setting PVID
	if (checkForExistence(win.s_pageview_id)) {
		setContextData('wb.mpvid', win.s_pageview_id);
	}

	// Prop15
	if (checkForExistence(omniture.s_sponsor_program) !== '' && defaultSettings.moduleName !== '') {
		setProp('prop15', `${omniture.s_sponsor_program}_${defaultSettings.moduleName}`);
	}

	if (checkForExistence(defaultSettings.moduleName) !== '') {
		moduleTracking(`${win.s_md.pageName.split('_').join('-')}_${defaultSettings.moduleName}`);
	} else {
		moduleTracking('');
	}

	// Linktext
	if (checkForExistence(win.s_linktext)) {
		setContextData('wb.lnktxt', win.s_linktext);
	}

	// Set Package Type
	if (checkForExistence(omniture.s_package_type)) {
		setProp('prop30', prop30(omniture).toLowerCase());
	}

	// Set New Registration Event
	if (checkForExistence(win.s_new_reg) !== '') {
		setEvent(win.s_new_reg);
	}

	// User Segment
	userSegment();

	// Embedded Asset
	const embAsset = checkForExistence(win.s_embedded_asset);
	if (embAsset && win.s_embedded_asset === true) {
		setContextData('wb.embast', 'embedded-asset');
	}

	// Exit or Download Link
	getExitDownloadLink(defaultSettings.moduleName);

	// Set New Metric
	const newMetricsArray = Object.entries(defaultOptions.metrics);
	newMetricsArray.forEach((metric) => {
		setNewMetric(metric[0], metric[1], 'adhoc', (originalValue, metricType) => {
			if (defaultOptions.resetMetrics) {
				resetMetrics.push({
					key: metric[0],
					value: typeof originalValue === 'undefined' ? '' : originalValue,
					type: metricType,
				});
			}
		});
	});

	// Dispatch Custom Event - onAdobeOmniBeforeAdhoc
	win.document.dispatchEvent(new CustomEvent('onAdobeOmniBeforeAdhoc'));

	// Reset code post tracking call
	win.document.addEventListener('onAdobeOmniPostTrack', () => {
		// Reset
		win.s_md.events = originalEvents;
		win.s_md.linkTrackVars = originalLinkTrackVars;
		win.s_md.contextData = originalContextData;

		// Reset Custom Props & Context Datas
		if (resetMetrics.length > 0) {
			resetMetrics.forEach((metric) => {
				if (metric.type === 'prop') {
					setProp(metric.key, metric.value);
				} else if (metric.type === 'contextData') {
					setContextData(metric.key, metric.value);
				}
			});
		}

		// Reset code based on if we have captured if once before
		setContextData('wb.adblocker', '');
		setProp('prop15', '');
		if (win.s_persist40 === '1') {
			setProp('prop40', '');
		}
		if (checkForExistence(win.s_new_reg) !== '') {
			win.s_new_reg = '';
		}
		if (checkForExistence(win.s_linktext) !== '') {
			win.s_linktext = '';
		}

		// Dispatch Custom Event - onAdobeOmniAfterAdhoc
		win.document.dispatchEvent(new CustomEvent('onAdobeOmniAfterAdhoc'));

		// Log
		log('info', 'FIRED WMDPAGELINK!!');
	}, {
		once: true,
	});

	// Fire Tracking Link Call
	win.s_md.tl(true, 'o', defaultSettings.moduleName);
};

/**
 * wmdTrack
 * @param {Object} settings key/values for sSource
 * @param {Object} options nothing used currently
 * This function takes the sSource string value and sets it in a ctr cookie.
 * We also set many other cookies such as (ctrTopicId, ctrContentClassification,
 * ctrHealthCenter, ctrPageviewId, prop29).These values get captured on dynamic
 * pageview calls and also subsequent pageview calls
 */
export const wmdTrack = (settings, options) => {
	let defaultSettings = {
		sSource: '',
		moduleName: '',
	};

	defaultSettings = Object.assign(defaultSettings, settings);

	if (defaultSettings.sSource !== '') {
		defaultSettings.moduleName = defaultSettings.sSource;
	}

	// Set CTR Cookie
	readWriteCookies(`${defaultSettings.moduleName}|`, 'write');

	// Log
	log('info', 'FIRED WMDTRACK!!');
};

export const wmdDpv = (settings, options) => {
	let defaultSettings = {
		pageName: '',
		moduleName: '',
		iCount: '',
		refresh: true,
		s_sponUri: '',
	};
	let defaultOptions = {
		metrics: [],
		resetMetrics: false,
	};

	defaultSettings = Object.assign(defaultSettings, settings);
	defaultOptions = Object.assign(defaultOptions, options);

	// Log
	log('info', 'FIRED WMDDPV!!');

	// If there is a moduleName lets track it in the next call
	if (defaultSettings.moduleName !== '') {
		wmdTrack({ moduleName: defaultSettings.moduleName });
	}

	// Create new Pageview ID
	createPvid();

	wmdPageview(defaultSettings, defaultOptions);

	// Refresh Ads after Pageview
	if (defaultSettings.refresh) {
		// Work with Michael on having Vue Ads refreshed
		if (typeof win.webmd !== 'undefined' && typeof win.webmd.ads !== 'undefined') {
			win.webmd.ads.refresh(defaultSettings.refresh);
		}
	}

	// Dispatch Custom Event - onAdobeOmniDpvFired
	win.document.dispatchEvent(new CustomEvent('onAdobeOmniDpvFired'));
};

export const wmdSearchTrack = (settings, options) => {
	let defaultSettings = {
		sSource: '',
		moduleName: '',
	};

	defaultSettings = Object.assign(defaultSettings, settings);

	if (defaultSettings.sSource !== '') {
		defaultSettings.moduleName = defaultSettings.sSource;
	}

	// Set CTR Cookie
	readWriteCookies(defaultSettings.moduleName, 'write');

	// Log
	log('info', 'FIRED WMDSEARCHTRACK!!');
};

export const wmdSetUserSegment = (segment, persistence, program) => {
	let userPersistence = persistence;

	if ((userPersistence !== '2') && (userPersistence !== '3')) {
		userPersistence = '1';
	}

	win.s_persist40 = userPersistence;

	setNewMetric('prop40', segment.toLowerCase());

	if (userPersistence !== '1') {
		setCookie('p40', `${segment}|${program}`);
	}

	// Log
	log('info', 'FIRED WMDSETUSERSEGMENT!!');
};

// Expose these functions globally
if (win) {
	win.wmdPageview = wmdPageview;
	win.wmdPageLink = wmdPageLink;
	win.wmdTrack = wmdTrack;
	win.wmdDpv = wmdDpv;
	win.wmdSearchTrack = wmdSearchTrack;
	win.wmdSetUserSegment = wmdSetUserSegment;
}
