// Helpers
import log from './helpers/logger';
import getWin from './helpers/get-window';
import getVersion from './helpers/get-version';
import getDomain from './helpers/get-domain';
import { setEvar, setContextData } from './helpers/set-omniture';
import getTrackingCookie from './helpers/get-tracking-cookie';
import readWriteCookies from './helpers/read-write-cookies';
import getAccount from './helpers/get-account';
import getTrackingServer from './helpers/get-tracking-servers';
import getQueryParam from './helpers/get-query-param';

// Trackers
import server from './trackers/server';
import pageName from './trackers/page-name';
import * as props from './trackers/props';
import * as contextDatas from './trackers/context-datas';
import moduleTracking from './trackers/module-tracking';
import matchbackCookie from './trackers/matchback-cookie';
import registration from './trackers/registration';
import userSegment from './trackers/user-segment';

// Core
import doPlugins from './do-plugins';
import { preTrackCallback, postTrackCallback } from './track-callbacks';
import initPv from './initial-pv';

const win = getWin();

if (win) {
	win.s_pageview_sent = false;
}

export default (options, reinit, doPV) => {
	const { omniture } = win.initialState.pagedata || {};

	if (!win) {
		return;
	}

	/**
	 * If we do not have store data we need to exit because
	 * we will not be able to kick off anything.
	 */
	if (omniture === undefined) {
		log('error', 'NO STORE DATA!! Could not setup and make intial PV call.');
		return;
	}

	/* Dispatch Custom Event - onAdobeOmniDataReady */
	win.document.dispatchEvent(new CustomEvent('onAdobeOmniDataReady'));

	if (!reinit) {
		// SET GLOBAL s_md OBJECT IN A EASIER REFRENCE
		win.s_md = {};

		// SETUP SECTION
		/*	Version */
		const sVer = getVersion(options.beaconType);

		/* sDomain */
		const sDomain = getDomain();

		// REPORT SUITE ID
		/**
		 * This is the most important function in here.
		 * It kicks off the setup for Analytics and creates
		 * our s_md object.
		 */
		win.s_md = win.s_gi(getAccount(omniture, options));

		// CONFIG SECTION
		win.s_md.cookieDomainPeriods = '2';
		win.s_md.jsCookieDomainPeriods = '2';
		win.s_md.charSet = 'ISO-8859-1';

		/* Conversion Config */
		win.s_md.currencyCode = 'USD';

		/* Link Tracking Config */
		win.s_md.trackDownloadLinks = false;
		win.s_md.trackExternalLinks = false;
		win.s_md.trackInlineStats = true;
		win.s_md.linkDownloadFileTypes = 'exe,zip,wav,mp3,mov,mpg,avi,wmv,doc,pdf,xls';
		win.s_md.linkInternalFilters = `javascript:, ${sDomain}`;
		win.s_md.linkLeaveQueryString = false;
		win.s_md.linkTrackVars = 'events,contextData.wb.lnktxt,contextData.wb.pddoc,contextData.wb.rspd,contextData.wb.brkpt,contextData.wb.embast,contextData.wb.cstm,contextData.wb.dwnld,contextData.wb.exiturl,contextData.wb.adblocker,contextData.wb.liveramp,contextData.wb.mlink,contextData.wb.mmodule,contextData.wb.mpage,contextData.wb.mtopic,contextData.wb.mpvid,contextData.wb.mhcenter,contextData.wb.mcontentc,contextData.wb.tapid,contextData.wb.gdprauth,prop3,prop4,prop6,prop15,prop21,prop24,prop28,prop29,prop30,prop31,prop33,prop40,prop47,prop48,eVar46,eVar45,eVar44';
		win.s_md.linkTrackEvents = '';
		win.s_md.visitorNamespace = 'webmd';
		win.s_md.usePlugins = true;

		// Tracking Servers
		win.s_md.registerPreTrackCallback(preTrackCallback);
		win.s_md.registerPostTrackCallback(postTrackCallback);

		/**
		 * WARNING: Changing any of the below variables will cause drastic
		 * changes to how your visitor data is collected. Changes should only be
		 * made when instructed to do so by your account manager
		 *
		 * Set-up the AppMeasurment tracking servers
		 */
		win.s_md.trackingServer = getTrackingServer('nonSecure', options);
		win.s_md.trackingServerSecure = getTrackingServer('secure', options);

		// DO PLUGINS SECTION
		/**
		 * This function is the last thing that gets fired before any call
		 * This is where you will place any adobe plugins
		 */
		win.s_md.doPlugins = doPlugins;

		// Adobe Plugins
		/* eslint-disable */
		win.s_md.apl = new Function("l", "v", "d", "u", ""
			+ "var s=this,m=0;if(!l)l='';if(u){var i,n,a=s.split(l,d);for(i=0;i<a."
			+ "length;i++){n=a[i];m=m||(u==1?(n==v):(n.toLowerCase()==v.toLowerCas"
			+ "e()));}}if(!m)l=l?l+d+v:v;return l");
		/* eslint-enable */

		// GLOBAL SETTINGS
		/* Server */
		win.s_md.server = server(omniture, sVer);
		log('info', `s_md.server: ${server(omniture, sVer)}`);
	}

	/* Page Name */
	win.s_md.pageName = pageName(omniture);
	log('info', `s_md.pageName: ${pageName(omniture)}`);

	if (!reinit) {
		/* Matchback Cookie */
		matchbackCookie();
	}

	/* Props */
	const propsToTrack = [3, 4, 6, 7, 9, 15, 21, 24, 29, 30, 31, 33, 36, 38, 45, 47, 48, 49];
	propsToTrack.forEach((propNum) => {
		const propId = `prop${propNum}`;

		// Make sure we have a function for each prop value we want to track
		if (typeof props[propId] === 'function') {
			// Run the function and store in the returned value
			const propFunc = props[propId](omniture).toString();

			// Check if returned value is not empty
			if (propFunc !== '') {
				// Set the Prop value
				win.s_md[propId] = propFunc.toLowerCase();
				log('info', `s_md.prop${propNum}: ${propFunc.toLowerCase()}`);
			} else {
				log('info', `s_md.prop${propNum}: .....`);
			}
		}
	});

	/* Context Datas */
	const contextDatasToTrack = ['store', 'plt', 'brkpt', 'icd', 'ecd', 'vapi', 'embast', 'cstm', 'liveramp', 'pbtp', 'pbnm', 'tpcot', 'lotprofid', 'pubsource', 'pubsourceco', 'metakywrd', 'intdemo', 'stgcond', 'mrt', 'titletag', 'tapid', 'gdprauth'];
	contextDatasToTrack.forEach((contextDataNum) => {
		const contextDataId = contextDataNum;

		// Make sure we have a function for each prop value we want to track
		if (typeof contextDatas[contextDataId] === 'function') {
			// Run the function and store in the returned value
			const contextDataFunc = contextDatas[contextDataId](omniture).toString();

			// Check if returned value is not empty
			if (contextDataFunc !== '') {
				// Set the Context Data value
				win.s_md.contextData[`wb.${contextDataId}`] = contextDataFunc.toLowerCase();
				log('info', `s_md.contextData.wb.${contextDataNum}: ${contextDataFunc.toLowerCase()}`);
			} else {
				log('info', `s_md.contextData.wb.${contextDataNum}: .....`);
			}
		}
	});

	/* Context Datas */
	// Capture Conversant cookie value in dynamic pageviews
	setEvar('eVar44', getTrackingCookie('DotomiUser'));
	log('info', `s_md.eVar44: ${getTrackingCookie('DotomiUser') !== '' ? getTrackingCookie('DotomiUser') : '.....'}`);

	// Capture values of evar46 in dynamic pageviews
	setEvar('eVar46', getTrackingCookie('turn'));
	log('info', `s_md.eVar46: ${getTrackingCookie('turn') !== '' ? getTrackingCookie('turn') : '.....'}`);

	/* Referring Page Information */
	readWriteCookies(null, 'read', (cookie) => {
		// Set CTR from previous page
		if (cookie.ctr !== '') {
			const ctrCookieRead = cookie.ctr.split('|');
			const ctrLocation = ctrCookieRead[0];

			// Set module tracking
			moduleTracking(ctrLocation);
		}

		// Set Topic ID from previous page
		if (cookie.ctrTopicId !== '') {
			setContextData('wb.mtopic', cookie.ctrTopicId);
		}

		// Set Content Classification from previous page
		if (cookie.ctrContentClassification !== '') {
			setContextData('wb.mcontentc', cookie.ctrContentClassification);
		}

		// Set Health Center from previous page
		if (cookie.ctrHealthCenter !== '') {
			setContextData('wb.mhcenter', cookie.ctrHealthCenter);
		}

		// Set Pageview ID from previous page
		if (cookie.ctrPageviewId !== '') {
			setContextData('wb.mpvid', cookie.ctrPageviewId);
		}
	});

	if (!reinit) {
		/* CTR Query String Parameter Tracking */
		const qctr = getQueryParam('ctr') || getQueryParam('CTR');
		if (qctr !== '') {
			// Set module tracking
			moduleTracking(qctr);
		}
	}

	/* User Segment */
	userSegment();

	/* Registration */
	registration();

	if (!reinit) {
		/* Dispatch Custom Event - onInitialPvFired */
		win.document.dispatchEvent(new CustomEvent('onAdobeOmniInitialized'));
	}

	/**
	 * Initial Pageview
	 * KEEP ON THE BOTTOM!!!
	 */
	if (!reinit || (reinit && doPV)) {
		initPv(win.initialState.pagedata.disable_omniture_load);
	}
};
