import getWin from './get-window';

const win = getWin();

const getResponsiveWidthType = () => {
	const width = win.innerWidth ? parseInt(win.innerWidth, 10) : parseInt(win.document.getElementsByTagName('body')[0].parentNode.offsetWidth, 10);

	let widthType;

	if (width < 640) {
		widthType = 5;
	} if (width > 639 && width < 768) {
		widthType = 4;
	} if (width > 767 && width < 1024) {
		widthType = 3;
	} if (width > 1023 && width < 1240) {
		widthType = 2;
	} if (width > 1239) {
		widthType = 1;
	}

	return widthType;
};

export default () => {
	let widthSize;

	switch (getResponsiveWidthType()) {
		case 5:
			widthSize = '0-639';
			break;
		case 4:
			widthSize = '640-767';
			break;
		case 3:
			widthSize = '768-1023';
			break;
		case 2:
			widthSize = '1024-1239';
			break;
		case 1:
			widthSize = '1240-greater';
			break;
		default:
			widthSize = null;
	}

	return widthSize;
};
