import getWin from './get-window';

const win = getWin();

export default (metaName) => {
	const metas = win.document.getElementsByTagName('meta');
	let metaValue = '';

	for (let i = 0; i < metas.length; i += 1) {
		if (metas[i].getAttribute('name') === metaName || metas[i].getAttribute('property') === metaName) {
			metaValue = metas[i].getAttribute('content');
		}
	}

	return metaValue.toLowerCase();
};
