import { getPvid } from 'webmd-core';
import getWin from '../helpers/get-window';
import { readCookie } from '../helpers/cookie-functions';
import checkForExistence from '../helpers/check-for-existence';

const win = getWin();

/**
 * Prop3 captures in Site name
 * @param {object} omniture omniture data from the store
 */
export const prop3 = (omniture) => checkForExistence(omniture.s_site, 'ntc');

/**
 * Prop4 captures in Topic Id
 * @param {object} omniture omniture data from the store
 */
export const prop4 = (omniture) => checkForExistence(omniture.s_topic);

/**
 * Prop6 captures in Buisness Reference
 * @param {object} omniture omniture data from the store
 */
export const prop6 = (omniture) => checkForExistence(omniture.s_business_reference);

/**
 * Prop7 captures in Asset Id
 * @param {object} omniture omniture data from the store
 */
export const prop7 = (omniture) => checkForExistence(omniture.s_asset_id);

/**
 * Prop9 captures in Page Number
 * @param {object} omniture omniture data from the store
 */
export const prop9 = (omniture) => checkForExistence(omniture.s_page_number);

/**
 * Prop15 captures in ctr cookie along with prop29 cookie from previous page
 */
export const prop15 = () => {
	const ctrCookie = readCookie('ctr').split('|');
	const ctrLocation = ctrCookie[0];
	const prop29LastPage = readCookie('prop29');
	const position = ctrLocation.indexOf('_');
	const sMl = ctrLocation.substr(position + 1).toLowerCase() || '';
	let previousPageSponorProgram = '';

	if (prop29LastPage !== '' && sMl !== '') {
		previousPageSponorProgram = `${prop29LastPage}_${sMl}`;
	}

	return previousPageSponorProgram;
};

/**
 * Prop21 captures in ctr cookie 2nd part
 */
export const prop21 = () => {
	const ctrCookie = readCookie('ctr').split('|');
	const ctrLocation = ctrCookie[1] || '';

	return ctrLocation;
};

/**
 * Prop24 captures in PVID
 */
export const prop24 = () => getPvid();

/**
 * Prop29 captures in Sponsor Program
 * @param {object} omniture omniture data from the store
 */
export const prop29 = (omniture) => checkForExistence(omniture.s_sponsor_program);

/**
 * Prop30 captures in Package Type
 * @param {object} omniture omniture data from the store
 */
export const prop30 = (omniture) => {
	let packageType = checkForExistence(omniture.s_package_type);
	packageType = packageType.toLowerCase();

	if (packageType !== '') {
		if (packageType.indexOf('- sponsored blog') === -1) {
			packageType = packageType.split(' - sp').join('');
		}

		packageType = packageType.split('-sp').join('');
		packageType = packageType.split(' - nosp').join('');
		packageType = packageType.split('-nosp').join('');

		if (checkForExistence(omniture.s_sponsor_program) !== '') {
			packageType += ' - sp';
		} else {
			packageType += ' - nosp';
		}
	}

	return packageType;
};

/**
 * Prop31 captures in Package Name
 * @param {object} omniture omniture data from the store
 */
export const prop31 = (omniture) => checkForExistence(omniture.s_package_name);

/**
 * Prop33 captures in Asset Name. It will also capture in
 * video module name if it is set globally and on a video page
 * @param {object} omniture omniture data from the store
 */
export const prop33 = (omniture) => {
	let assetName = checkForExistence(omniture.s_assetname);

	if ((typeof omniture.s_business_reference !== 'undefined' && omniture.s_business_reference === 'Feature - Video') && typeof win.s_video_module_name !== 'undefined') {
		assetName = win.s_video_module_name;
	}

	return assetName;
};

/**
 * Prop36 captures in Topic Directory.
 * @param {object} omniture omniture data from the store
 */
export const prop36 = (omniture) => {
	let topicDir = checkForExistence(omniture.s_topic_dir);

	if (topicDir !== '') {
		topicDir = `tpdr-${topicDir}`;
	}

	return topicDir;
};

/**
 * Prop38 captures in Package Name
 * @param {object} omniture omniture data from the store
 */
export const prop38 = (omniture) => checkForExistence(omniture.s_channel_health);

/**
 * Prop45 captures in Sponsor URI set in ./trackers/page-name.js
 * Also attaches s_tug if that is available
 * @param {object} omniture omniture data from the store
 */
export const prop45 = (omniture) => {
	let sponsorUri = checkForExistence(win.s_sponUri);

	if (checkForExistence(omniture.s_tug) !== '' && sponsorUri !== '') {
		sponsorUri = `${omniture.s_tug}_${sponsorUri}`;
	}

	return sponsorUri;
};

/**
 * Prop47 captures Matchback cookie
 */
export const prop47 = () => readCookie('WBMD_MB9') || readCookie('WBMD_MB');

/**
 * Prop48 VueJS Flag
 * Lets BI know that these pages are built on new VueJS Runtime
 */
export const prop48 = () => 'vue';

/**
 * Prop49 captures VisitorId cookie
 */
export const prop49 = () => readCookie('VisitorId');
