import getWin from './helpers/get-window';
import log from './helpers/logger';

const win = getWin();

/**
 * Initial Pageview is kicked off by the store value disable_omniture_load
 * If this is set to true we will not make the PV call.
 */
export default (disable) => {
	if (typeof disable === 'undefined' || !disable) {
		// Before Pageview Event
		win.document.dispatchEvent(new CustomEvent('before_pv'));
		win.document.dispatchEvent(new CustomEvent('onAdobeOmniBeforePv'));

		// Dispatch after events after post tracking call
		win.document.addEventListener('onAdobeOmniPostTrack', () => {
			// After Pageview Event
			win.s_pageview_sent = true;
			win.document.dispatchEvent(new CustomEvent('after_pv'));
			win.document.dispatchEvent(new CustomEvent('onAdobeOmniAfterPv'));
			win.document.dispatchEvent(new CustomEvent('onAdobeFireSaftyCheck'));

			log('info', 'FIRED INITIAL PV!!!');
		}, {
			once: true,
		});

		// FIRE Initial PV Call
		win.s_md.t();

		win.biConsumerIsReady = true;
	} else {
		win.document.dispatchEvent(new CustomEvent('onAdobeFireSaftyCheck'));
		win.biConsumerIsReady = true;
	}
};
